html,body {
	box-sizing: border-box;
	background: yellow;
}
*,*:before,*:after {
	outline: none !important;
	box-sizing: inherit;
}
body {
	font-family: $base-font;
	font-size: $em-base;
	font-weight: $base-font-weight;
	color:$base-color;
}
.wrapper {
	width: 92%;
	margin: 0px auto;
}
.wrapper.first{
	max-width: 1300px;
	width: 100%;
}
a { 
	color: inherit;
	text-decoration: inherit;
}